.w3-modal {
  z-index:3;
  display:block;
  padding-top:100px;
  position:fixed;
  left:0;
  top:0;
  width:100%;
  height:100%;
  overflow:auto;
  background-color:rgb(0,0,0);
  background-color:rgba(0,0,0,0.4);
  .w3-modal-content {
    margin:auto;
    background-color:#fff;
    position:relative;
    padding:10px 20px;
    outline:0;
    width:300px;
    text-align: center;
    svg > path{
      color: gray;
    }
    svg {
      animation: spin 1500ms linear infinite;
    }
    .svg-inline--fa {
      font-size: 5em;
    }
  }
}

@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

  
@media (orientation: portrait) and (min-width: 1px) {
  nav {
    background-color: black;
    padding: 5px 0px;
    .mainMenu {
      padding: 5px 0px;
    }
    ul {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: fit-content;
      margin-left: auto;
      margin-right: auto;
      li {
        display: block;
        &:not(:first-child) {
          border-left: 0;
        }
        a {
          display: block;
          cursor: pointer;
          float: left;
          transition-property: background-color;
          padding: 5px 3px;
          text-decoration: none;
          text-transform: uppercase;
          span {
            color: lightgrey;
            font-size: .8rem;
            margin-left: 5px;
          }
          svg > path{
            fill: lightgrey;
          }
          &.active {
            span {
              color: white;
              text-decoration: underline;
              font-weight: 700;
            }
          }
          &:hover span {
            color: white; 
          }
        }
      }
    }
  }
}

@media (orientation: portrait) and (min-width: 360px) {
  nav ul li a {
    padding: 5px 15px;
    span {
      font-size: 1rem;
    }
  }
}

@media (orientation: portrait) and (min-width: 768px) {
  nav ul li a {
    padding: 10px 35px;
    span {
      font-size: 1.5rem;
    }
    .svg-inline--fa {
      font-size: 1.5rem;
    }
  }
}

@media (orientation: portrait) and (min-width: 1024px) {
  nav ul {
    margin: 0 30px;
    li a {
      padding: 10px 45px;
      span {
        font-size: 1.6rem;
      }
      .svg-inline--fa {
        font-size: 1.6rem;
      }
    }
  }
}

